import React, {useState} from 'react'
import {Navbar,NavbarBrand, Jumbotron, Nav, NavbarToggler,Collapse,NavItem,Button} from 'reactstrap'
import { Link,NavLink } from 'react-router-dom'

import Banner from './banner'

function Header(props){


    const [strings, setStrings] = useState(["All our dreams can come true","MERN stack Web Development",
                                     "Machine Learning and NLP","Blockchain Development",
                                     "Python developer"]);
    const [index, setIndex] = useState(0);
    const [toggle, setToggle] = useState(false);
    const rightHandler = () =>{ setIndex((index +1)%5) }
    const leftHandler = () =>{ setIndex(index-1 >= 0 ? index-1: 4) }
    const toggleNav = ()=>{ setToggle(toggle?false:true) }
    return(
        <div id="header ">
            {/* <nav className="header-1 navbar  navbar-expand-md  ">
                <Link to={'/home'} ><div className="links "> Bikrant Kumar </div></Link> 
                <button className="navbar-toggler" type="button" data-toggle="collapse" 
                data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" 
                aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">
                            <Link to={'/home'} ><div className="links nav-link nav-item fa fa-home fa-lg"> Home </div></Link> 
                        </li>
                        <li className="nav-item">
                            <Link to={'/aboutme'}><div className="links nav-link fa fa-info fa-lg"> About me </div></Link> 
                        </li>
                        <li className="nav-item">
                            <Link to={'./resume'}><div className="links nav-link fa fa-address-card fa-lg"> Resume </div></Link>
                        </li>
                        <li className="nav-item">
                            <Link to={'./projects'}><div className="links nav-link fa fa-cogs fa-lg"> Projects  </div></Link>
                        </li>
                    </ul>
                </div>
            </nav>  */}
            <Navbar dark expand ="md" >
            
                    <div className="container">
                        
                        <NavbarToggler onClick={ toggleNav }/>
                        <NavbarBrand className="mr-auto brand" href="/">
                            Bikrant Kumar
                        </NavbarBrand>
                        <Collapse isOpen={toggle} navbar>
                        <Nav navbar className="navlinks">
                                <NavItem>
                                    <NavLink className="nav-link " to="/home" ><span className="fa fa-home " ></span> Home</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to='/aboutme'><span className="fa fa-info "></span> About </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link"  to='/resume'><span className="fa fa-address-card "></span> Resume</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to='/projects'><span className="fa fa-cogs "></span> Projects</NavLink>
                                </NavItem>
                        </Nav>
                        </Collapse>
                        </div>
                        </Navbar>
                   
            
            <div className="header-2">
    
                <div className="background">
                
                     <Banner index = {index}/> 
                    {/* <img src={images[index]} alt="web" className="banner"/> */}
                </div>

                <div className="content">
                    <div className="left" onClick = { leftHandler}>
                        <div className="vButton btn-block"><span className="fa fa-arrow-down"></span></div>
                    </div>
                    <div className="middle">
                        <h1 className="headline">{strings[index]}</h1>
                        <Link to={'./contact'}><button className="btn btned links btn-primary mt-5">Contact for 
                        Colabration</button></Link>
                    </div>
                    <div className="right " onClick={ rightHandler }>
                        <div className="vButton btn-block"><span className="fa fa-arrow-up"></span></div>
                    </div>
                </div> 
            </div>
        </div>
    );
}
export default Header;