import {useState} from 'react'
import intro from './Presentation1.mp4'
import block from './videoplayback.mp4'
import web from './1.jpeg'
import python from './3.jpeg'
import ML from './4.jpg'
function Banner(props){
    const [images, setImages] = useState([intro,web,ML,block,python,]);
    if( props.index === 0 || props.index === 3){
        return <video autoPlay muted className="banner">
                    <source src={images[props.index]} type='video/mp4' />
                </video>;
    }
    else{
        return <img src={images[props.index]} alt="web" className="banner"/>
    }
    
}
export default Banner